/*_________________________ Grid _________________________ */
.grid {
	$gutter: 20px;
	$gutter-sm: 20px;
	$col-1: 100%/12;
	&__row {
		position:relative;
		width: 100%;
		min-height: 1px;
	}
	&__container {
		position:relative;
		display: flex;
		flex-wrap: wrap;
		padding: 0 0px;
		margin-left: auto;
		margin-right: auto;
		max-width: $container-max-width;
		width: 100%;
		@include under(sm) {
			max-width: 100%;
		}
	}
	&__col {

		&-1-1,
		&-1-2,
		&-1-3 {
			position:relative;
		}
		&-1-1 {
			width: 100%;
			@include under(sm) {
				padding: 0px 20px 0px 20px;
			}
		}
		&-1-2 {
			@include binary-column-maker($gutter:$gutter);
			&:nth-of-type(even) {
				padding-right: 20px;
				padding-left: 20px;
			}
			&:nth-of-type(odd) {
				padding-left: 20px;
				padding-right: 20px;
			}

			@include under(sm) {
				width: 100%;
				padding:0 20px !important;
			}
		}

		&-1-3{
			@include ternary-column-maker($gutter:$gutter)
			@include under(sm) {
				width: calc(#{$col-1} * 12);
				padding: 0px 20px 0px 20px !important;
			}

			@include under(xs) {
				padding: 0px 20px 0px 20px !important;
			}
		}

		&-1-4{
			@include cuaternary-column-maker($gutter:$gutter)
		}

		&-1-6{
			width: calc(#{$col-1} * 6);
			padding-right: $gutter/2;
			padding-left: 0;
			@include under(sm-big) {
				width: calc(#{$col-1} * 8);
				padding-right: 0;
				padding-left: 0;
			}
			@include under(xs-big) {
				width: calc(#{$col-1} * 12);
				padding-right: 0;
				padding-left: 0;
			}

		}
		&-1-8 {
			width: calc(#{$col-1} * 8);
			padding-right: 0px !important;
			padding-left: 10px;
			// @include under(sm) {
			// 	width: calc(#{$col-1} * 10);
			// 	margin-left: $col-1 !important;
			// }
			@include under(sm-big) {
				width: 100%;

			}
			@include under(xs-big) {
				width: 100%;
				margin-left: 0 !important;
			}
		}

		&-1-9 {
			width: calc(#{$col-1} * 9);
			padding-right: 10px !important;
			padding-left: 10px;
			// @include under(sm) {
			// 	width: calc(#{$col-1} * 10);
			// 	margin-left: $col-1 !important;
			// }
			@include under(sm) {
				width: 100%;
			}
		}
	}

	&__box {
		position:relative;
		width: 100%;
	}
}


